<template>
  <div id="wangeditor">
    <div ref="editorElem" style="height: 200px;"></div>
  </div>
</template>
<script>
  import E from "wangeditor";
  import { markdown } from 'UTIL/markdown'
  export default {
    name: "Editor",
    data() {
      return {
        editor: null,
        editorContent: this.describe
      };
    },
    // catchDatahtml
    props: ['describe', 'type'], // 
    watch:{
     describe:function(newQuestion, oldQuestion) {
       if(this.editorContent.length === 0 && this.type === 'up'){
         let html = markdown(this.describe)
         this.editor.txt.html(html)
        // this.editor.cmd.do('insertHTML', this.describe)
       }
     } 
    },
    mounted() {
      this.editor = new E(this.$refs.editorElem);
      // html
      this.editor.customConfig.onchange = html => {
        this.editorContent = html;
        this.$emit('catchData', this.editorContent); // htmlcatchData
      };
      this.editor.customConfig.menus = [
        // 
        'head', // 
        'bold', // 
        'fontSize', // 
        'fontName', // 
        'italic', // 
        'underline', // 
        'strikeThrough', // 
        'foreColor', // 
        'backColor', // 
        'link', // 
        'list', // 
        'justify', // 
        'quote', // 
        'emoticon', // 
        //'image', // 
        'table', // 
        'code', // 
        'undo', // 
        'redo' // 
      ];
      this.editor.create(); // 
     }
  }
</script>

<style>
#wangeditor{width: 40%; height: auto; float: left;}
.w-e-text-container{height: 170px !important;}
.w-e-menu{
  z-index: 2 !important;
}
  .w-e-text-container{
    z-index: 1 !important;
  }
</style>



